/*! `Custom` Bootstrap 4 theme */
@import "../../node_modules/bootstrap/scss/functions.scss";
@import "../../node_modules/bootstrap/scss/variables.scss";

@import url("https://use.typekit.net/aoy1qwx.css");


$headings-font-family: 'gill-sans-nova', sans-serif, "HelveticaNeue-Bold", "Helvetica Neue", sans-serif;

$headings-font-weight: 500;

h1, h2, h3, h4, h5 {
// font-weight: 500 !important;
font-style: normal !important;
// margin-bottom: 1.5rem !important;
}

p {
// font-style: normal !important;
// margin-bottom: 1.5rem !important;
}

// Example usage:
$primary: #25B3B7;
$primary-dark: #078590;
$secondary: #EDF2F2; 
$text-dark: #363636;
$text-light: #FFFFFF;
$accent: #BD2C88;
$darker: #000000;
$back: #a9a9a9;
$content: #ffffff;
$success:#28a745;
$danger:#FF0000;
$info:#9BCDFA;
$warning:#ffc107;
$light:#FFFFFF;
$ptitle:#3d5c73;
$titletext: #000000;
$form-input: #e4e4e4;
$text: #ffffff;

$enable-gradients:true;

$spacer: 2.2rem;
$btn-border-radius: 1.35rem;

$theme-colors: ( 
"primary": $primary,
"danger": $danger,
"back": $back,
"content": $content,
"secondary": $secondary,
"success": $success,
"info": $info,
"warning": $warning,
"light": $light,
"dark": $dark,
"titletext": $titletext,
"form-input": $form-input,
"ptitle" : $ptitle,
);

$headings-color: $primary;

.radius-lg{
    border-radius: 2rem;
}

body, #content {
    color: $text;
    font-size: 0.9rem;
    line-height: 1.8;
    font-weight: 300;
}

.hover-block{
    position: relative; 
    top: 0px;
    transition: all 0.3s ease 0.3s;
    box-shadow: 0px 5px 10px -5px rgba(108,110,135,0.4) !important;
&:hover {
    top: -20px;
    box-shadow: 0px 50px 30px -20px rgba(108,110,135,0.3) !important;
    }
}


ul li {
    margin: 4px;
    // margin-left: 20px;
}


ul li:first-letter {
    // margin-bottom: 8px;
    text-transform: capitalize;
}

.z-index-100 {
    z-index: 100;
    position: relative;
}

.fill-primary{
    fill: $primary;
}

.fill-secondary{
    fill: $secondary;
}

.fill-content{
    fill: $content;
}

a {
 color: $ptitle;   
}

.page-numbers {
    display: flex;
    justify-content: center;
    list-style: none;
    background: $light;
}

.page-numbers li{
  padding: 1rem;
}

.page-numbers li:hover{
  background: $primary;
}

.page-numbers li a{
  color: $secondary;
  background: transparent;
}

.opacity-gradient-primary {
   background: rgba(5, 35, 41, 0) linear-gradient(180deg, rgba(5, 35, 41, 0), $primary) repeat-x !important
}


.opacity-gradient-secondary {
   background: rgb(0,255,179);
   background: linear-gradient(159deg, rgba(0,255,179,1) 15%, rgba(0,164,255,1) 85%);
}

.opacity-gradient-content {
   background: rgba(255, 255, 255, 0) linear-gradient(180deg, rgba(255, 255, 255, 0), $content) repeat-x !important
}

.opacity-gradient-light {
   background: rgba(255, 255, 255, 0) linear-gradient(180deg, rgba(255, 255, 255, 0), $light) repeat-x !important
}

