    content: url('../img/icons/primary/bb-glow.svg');
}

.cellulite-primary { 
    content: url('../img/icons/primary/cellulite.svg');
}

.eye-treatment-primary { 
    content: url('../img/icons/primary/eye-treatment.svg');
}

.face-cryolipolysis-primary { 
    content: url('../img/icons/primary/cryolipolysis.svg');
}

.skin-treatments-primary { 
    content: url('../img/icons/primary/face-skin-treatments.svg');
}

.massage-primary { 
    content: url('../img/icons/primary/face-profile.svg');
}

.hair-primary { 
    content: url('../img/icons/primary/hair.svg');
}

.laser-hair-primary { 
    content: url('../img/icons/primary/laser-hair.svg');
}


.face-skin-treatments-primary { 
    content: url('../img/icons/primary/face-skin-treatments.svg');
}

.laser-lipo-primary { 
    content: url('../img/icons/primary/laser-lipo.svg');
}

.nails-primary { 
    content: url('../img/icons/primary/nails.svg');
}

.none-surgical-face-treatment-primary { 
    content: url('../img/icons/primary/none-surgical-face-treatment.svg');
}

.skin-wax-primary { 
    content: url('../img/icons/primary/wax.svg');
}

.massage-primary { 
    content: url('../img/icons/primary/massage.svg');
}

.pregnant-primary { 
    content: url('../img/icons/primary/pregnant.svg');
}

.icon-wrapper { 
    max-width: 150px;
}

.page-numbers current {
    font-weight: 800;
    background: transparent;
}

.top-0 {
    top: -1px;
}


.bottom-0 {
    bottom: -1px;
}


.left-0 {
    left: 0px;
}

.right-0 {
    right: 0px;
}

.icon-fill {
    fill: $ptitle;
}


.ease-in, .fill-secondary, .fill-primary {
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.video-responsive{
    overflow:hidden;
    padding-bottom:56.25%;
    position:relative;
    height:0;
}

.video-responsive iframe{
    left:0;
    top:0;
    height:100%;
    width:100%;
    position:absolute;
}

.sidebar-nav li a {
    color: $light;
    padding-left: 10px;
}

.wp-spacer-sm{
    height: 50px;
    width:100%;
    display: block;
}

.wp-spacer-md{
    height: 75px;
    width:100%;
    display: block;
}

.wp-spacer-lg{
    height: 100px;
    width:100%;
    display: block;
}

.wp-spacer-xl{
    height: 150px;
    width:100%;
    display: block;
}